body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #c9e62a;
  font-family: "Josefin Sans", sans-serif;
}
div {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -30px;
}
h1 {
  padding: 20px 20px;
  color: #fa9191;
  background-color: #ffe9c5;
  border-radius: 15px;
}
span {
  color: #eb6383;
}
